<template>
    <div>
        <el-space>
            <img
                    slot="cover"
                    :src="InfoBook.pic_url"
                    alt=""
            />
            <a-card-meta :title="InfoBook.name">
                <template slot="description">
                  <p class="first">书名：{{ dialog.name }} </p>
                  <p>作者：{{ dialog.author }}</p>
                  <p>出版时间: {{dialog.publish_time}}</p>
                  <p>图书简介：{{ dialog.b_intro }}</p>
                  <p>作者简介:{{ dialog.author_intro }}</p>
                  <p>评分：{{ dialog.score_avg }}</p>
                </template>
                <!--                  隐语义模型协同过滤-->
            </a-card-meta>
            <a-row style="margin-top: 30px;">
                <h1 style="font-size: 2rem;" id="month">
                    <a-icon type="trophy" id="RY" />&emsp;隐语义模型协同过滤【电影相似性】
                </h1>
            </a-row>
            <a-spin size="large" :spinning="loading">
                <div class="ant-pro-pages-list-projects-cardList">
                    <a-list
                            :loading="loading"
                            :data-source="RecsBook"
                            :grid="{ gutter: 24, xl: 4, lg: 3, md: 3, sm: 2, xs: 1 }"
                    >
                        <a-list-item slot="renderItem" slot-scope="item">
                            <a-card class="ant-pro-pages-list-projects-card" hoverable>
                                <el-space>
                                    <img
                                            slot="cover"
                                            :src=item.pic_url
                                            :alt="item.name"
                                    />
                                    <a-card-meta :title="item.name">
                                        <template slot="description">
                                            <p>书名：{{ item.name }} </p>
                                            <p>作者：{{ item.author }}</p>
                                            <p>图书简介：{{ item.b_intro_short }}</p>
                                            <p>评分：{{ item.score_avg }}</p>
                                        </template>
                                    </a-card-meta>
                                </el-space>
                            </a-card>
                        </a-list-item>
                    </a-list>
                </div>
            </a-spin>

            <!-- 内容相似性 -->
            <a-row style="margin-top: 30px;">
                <h1 style="font-size: 2rem;" id="months">
                    <a-icon type="trophy" id="RYs" />&emsp;内容相似性推荐
                </h1>
            </a-row>
            <a-spin size="large" :spinning="loading">
                <div class="ant-pro-pages-list-projects-cardList">
                    <a-list
                            :loading="loading"
                            :data-source="RecsBCBook"
                            :grid="{ gutter: 24, xl: 4, lg: 3, md: 3, sm: 2, xs: 1 }"
                    >
                        <a-list-item slot="renderItem" slot-scope="item">
                            <a-card class="ant-pro-pages-list-projects-card" hoverable>
                                <el-space>
                                    <img
                                            slot="cover"
                                            :src=item.pic_url
                                            :alt="item.name"
                                            @click=" switchInnerDialog(item.bid)"
                                    />
                                    <a-card-meta :title="item.name">
                                        <template slot="description">
                                            <p>书名：{{ item.name }} </p>
                                            <p>作者：{{ item.author }}</p>
                                            <p>图书简介：{{ item.b_intro_short }}</p>
                                            <p>评分：{{ item.score_avg }}</p>
                                        </template>
                                    </a-card-meta>
                                </el-space>
                            </a-card>
                        </a-list-item>
                    </a-list>
                </div>
            </a-spin>
        </el-space>
        <el-dialog :visible="innerVisible">
            <a-card-meta :title="InfoInnerBook.name">
                <img
                        slot="cover"
                        :src="InfoInnerBook.pic_url"
                        alt=""
                />
                <template slot="description">
                    <p>书名：{{ InfoInnerBook.name }} </p>
                    <p>作者：{{ InfoInnerBook.author }}</p>
                    <p>图书简介：{{ InfoInnerBook.b_intro_short }}</p>
                    <p>评分：{{ InfoInnerBook.score_avg }}</p>
                </template>
            </a-card-meta>
            <div slot="footer" class="dialog-footer">
                <el-button @click="innerVisible = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>

</template>

<script>
    import {getBookInfo, getBookInfoRec, getBookInfoRecBC} from "../../api/MovieRest";

    export default {
        name: "BookDetail",
        data(){
            return{
                InfoInnerBook: [],
                innerVisible: false,
            }
        },
        mounted() {
            this.bookDetail()
        },
        methods:{
            bookDetail(){
                const bid = window.sessionStorage.getItem("bid")
                getBookInfo({bid}).then((res)=>{
                    this.InfoBook=res.data  //这个地方需要注意   res.data.   res.
                });
                getBookInfoRec({bid}).then((res)=>{
                    this.RecsBook=res.data
                });
                getBookInfoRecBC({bid}).then((res)=>{
                    this.RecsBCBook=res.data
                })
            },
            switchDialog(bid){
                innerVisible=true
                getBookInfo({bid}).then((res)=>{
                    this.InfoInnerBook=res.data  //这个地方需要注意   res.data.   res.
                });
            },
        }
    }
</script>

<style scoped>

</style>